@import "src/styles/_fonts.scss";
.loginForm {
  width: 60%;
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 2px 4px 1px #0000001d;
  max-width: 400px;
  box-sizing: border-box;
  width: 100%;

  &__formItem {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
  }

  &__label {
    color: #4a4a4a;
    font-size: 20px;
    margin-bottom: 10px;
  }

  &__input,
  &__select {
    color: #4a4a4a;
    background-color: #fff;
    font-size: 1rem;
    padding: 10px 15px;
    border: 2px solid #c2e0ff;
    border-radius: 6px;
    width: 100%;
    box-sizing: border-box;
  }

  &__select {
    cursor: pointer;
  }

  &__buttonWrapper {
    text-align: end;
  }

  &__buttonSubmit {
    color: #fff;
    background-color: #33b5e5;
    padding: 10px 30px;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    border: none;
    border-radius: 8px;
    transition: all 0.3s ease;

    &:hover {
      background-color: #0099cc;
    }

    &:active {
      background-color: #007bb5;
    }
  }
}
