@import "src/styles/_fonts.scss";
.meetings {
  background-color: #fff;
  box-shadow: 0 2px 4px 1px #0000001d;
  border-radius: 10px;
  padding: 32px;
  margin-bottom: 40px;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  &__Button {
    text-decoration: none;
    color: #fff;
    background-color: #33b5e5;
    padding: 10px 30px;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    border: none;
    border-radius: 8px;
    transition: all 0.3s ease;
    &:hover {
      background-color: #0099cc;
    }

    &:active {
      background-color: #007bb5;
    }
  }

  &__cardsContainer {
    max-height: 400px; /* Ограничение высоты */
    overflow-y: auto; /* Прокрутка вниз */
    // border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    background-color: #f9f9f9;
  }

  &__card {
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 15px;
    margin-bottom: 10px;
    // background-color: #f9f9f9;
    background-color: #fff;
  }

  &__cardHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
